@import "_mixins.scss";


.program-title {
  padding-bottom: 76px;
  text-align: center;
}

.program {
  text-align: center;

  h2 {
    @include blueo;
    margin-top: 125px;
  }

  p {
    font-family: 'Indie Flower', cursive;
    @include gray;
    font-size: 25px;
  }
}

.program-big {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  margin-bottom: 120px;
  max-width: 1160px;
  // margin: 0 auto;

}

// .program-all{
// margin-left: 80px; 
//    margin-right:  80px;                       

// }


.program-box {

  display: inline-block;

  width: 560px;
  height: 300px;
  border-radius: 8px;
  box-shadow: 3.5px 3.5px 20px 0 rgba(0, 0, 0, 0.1);
  background-color: #ffffff;

  margin: 0px 20px 20px 0;
  overflow: hidden;

  & img {
    float: left;
    margin-right: 30px;

  }

  & h3 {
    text-align: left;
    @include gray;
    font-size: 20px;
    margin-top: 30px;

  }

  & p {
    text-align: left;
    @include gray-box;
    font-size: 14px;
    margin-bottom: 18px;
    line-height: 1.7;
  }

  .program-box-text-min {
    color: #999999;
  }
}

/*--------img-block------------*/

.img-block {

  line-height: 0;
  margin-bottom: 125px;
  text-align: center;

  & a {
    display: inline-block;
    width: 474px;
    height: 349px;
    position: relative;
    overflow: hidden;

  }

  & .descr {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(8, 68, 153, 0.84);
    color: #ffffff;
    padding-top: 150px;
    text-align: center;
    opacity: 0;
    transition: all .5s;
    font-size: 100px;
  }

  & a:hover .descr {
    top: 0;
    display: block;
    opacity: 1;
  }
}

// media----------------------------
@media (max-width: 960px) {
.img-block{
  margin-bottom: 40px;
}

}

@media (max-width: 570px) {
  .program-title {
    padding-bottom: 0px;

  }

  .program {

    h2 {
      padding-bottom: 0px;
      margin-top: 35px;
      margin-bottom: 0px;
    }

    p {
      margin-top: 0px;
    }

  }

  .program-big {
    margin-bottom: 80px;
  }

  .program-box {
    display: flex;
    flex-flow: column wrap;
    height: auto;
    align-items: center;
    margin-right: 0px;

    img {
      max-width: 270px;
      width: 100%;
    }

    .program-box-text {
      max-width: 300px;

      p {
        width: 262px;
      }
    }
  }

  .img-block a {
    max-width: 350px;
  }
}

@media (max-width: 400px) {
  .img-block a {
    max-width: 300px;
  }
}