@import "_mixins.scss";

.menu_logo {
  margin-top: 5px;
}

.menu-min {
  display: none;
}

.menu_top {
  display: flex;
  justify-content: space-around;
}

.menu_right .menu_ul>li:first-child {
  padding-left: 0;
}

.menu_left li,
.menu_right li {
  margin-top: 20px;
}

nav {
  height: 80px;
  font-weight: bold;

  & ul {
    padding: 0;
    margin: 0;
  }

  li {
    float: left;
    padding-left: 1em;
  }

  a {
    @include gray;
    padding-bottom: 5px;

    &:hover {
      @include blueo;
      position: relative;

      &:after {
        content: "";
        position: absolute;
        left: 25%;
        bottom: 0;
        height: 1px;
        width: 50%;
        border-bottom: 2px solid #5b6ceb;
      }
    }
  }
}

/// block form----------------------------
.fon_img {
  background: linear-gradient(rgba(79, 91, 196, 0.842), rgba(110, 112, 226, 0.575)), url(../img/fon-header.png) no-repeat center center;
  background-size: cover;
  height: auto;
}

.fon_box_container {
  display: flex;
  justify-content: center;
}

.fon_box {
  margin-right: 8%;
  margin-top: 8%;
  width: 300px;
  height: 391px;
  border-radius: 20px;
  box-shadow: 5.7px 5.7px 10px 0 rgba(0, 0, 0, 0.15);
  background-color: #ffffff;
}

.fon_box h2 {
  @include blueo;
  margin-left: 33px;
}

.form-control {
  margin-left: 33px;
  margin-top: 17px;
  width: 230px;
  height: 50px;
  border-radius: 8px;
  border: none;
  background-color: #f2f2f2;
}

select {
  border: none;
  background-color: #f2f2f2;
  @include gray;
  padding: 12px 10px;


}

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}

.btn-menu {
  width: 170px;
  height: 50px;
  border-radius: 8px;
  box-shadow: 2.1px 2.1px 5px 0 rgba(0, 0, 0, 0.15);
  background-color: #5b6ceb;
  color: #fff;
  font-size: 16px;
  margin-left: 95px;
  cursor: pointer;
}

.fon_box_text {
  padding-top: 10%;
  padding-bottom: 10%;
  max-width: 500px;

  h1 {
    font-family: 'Open Sans', sans-serif;
    font-size: 48px;
    font-weight: bold;
    line-height: 1.5;
    color: #ffffff;
  }

  p {
    font-family: 'Indie Flower', cursive;
    font-size: 25px;
    font-weight: normal;
    line-height: 2em;
    color: #ffffff;
    width: 280px;
  }
}

// media ----------------------------

@media (max-width: 890px) {

  .fon_box_text h1 {
    font-size: 40px;
  }
}

@media (max-width: 768px) {
  .fon_box_text {
    max-width: 200px;

    h1 {
      font-size: 32px;
    }

    p {
      max-width: 200px;
    }
  }
}

@media (max-width: 570px) {
  .fon_box_container {
    flex-direction: column;
    align-items: center;

    .fon_box {
      margin-right: 0;
    }

    .fon_box_text {
      text-align: center;
      margin: 0 auto;

      h1 {
        width: 190px;
        font-size: 26px;
      }
    }
  }

  .menu_top {
    justify-content: start;
    margin-left: 25px;
  }

  .menu_left {
    display: none;
  }

  .menu_right {
    display: none;
  }

  .menu-min {
    display: block;
  }

  .menu-link {
    width: 50px;
    height: 50px;
    background: rgb(247, 243, 243);
    border-radius: 50%;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, .4);
    position: fixed;
    top: 10px;
    right: 10%;
    z-index: 11;
  }

  .menu-link span,
  span:before,
  span:after {
    height: 2px;
    width: 18px;
    position: absolute;
    left: 50%;
    margin-left: -9px;
    background: #333;
    top: 50%;
    margin-top: -1px;
  }

  .menu-link span:before {
    transition: all 0.2s;
    content: '';
    margin-top: -6px;
  }

  .menu-link span:after {
    transition: all 0.2s;
    content: '';
    margin-top: 4px;
  }

  .menu-link_active span {
    background-color: transparent;
  }

  .menu-link_active span:before {
    transform: rotate(135deg);
    margin-top: 0;
  }

  .menu-link_active span:after {
    transform: rotate(-135deg);
    margin-top: 0;
  }

  .menu-tu {
    opacity: .9;
    background-color: #111d7a;
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 8;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.4s;
    left: -100vw;
  }

  .menu-tu a {
    display: block;
    width: 100%;
    font-weight: 600;
    letter-spacing: 3px;
    line-height: 4;
    color: rgb(179, 179, 179);
    transition: all 0.2s;
  }

  .menu-tu a:hover {
    text-decoration: none;
    color: rgb(185, 89, 89);
  }

  .menu-tu ul li {
    width: 100vw;
  }

  .menu-tu ul {
    list-style: none;
    padding: 0;
    text-align: center;
  }

  .menu_active {
    left: 0px;
  }
}